.App {
  width: 100vw;
  height: 100vh;
}

h1{
  text-align: center;
}

.outer {
  display: flex;
  width: 100%;
  height: 70%;
  background-color: aquamarine;
  padding: 15px;
  /* overflow: scroll; */
  /* align-items: center; */
  /* justify-content: center; */
}

.displayBoard {
  width: 50%;
  display: flex;
  flex-wrap: wrap;
  overflow: scroll;

}

.formContainer{
  width:40%;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.formContainer input{
  width: 500px;
  margin: 10px;
  height: 50px;
  font-size: 35px;
}

.formContainer textarea{
  width: 500px;
  margin: 10px;
  height: 150px;
  font-size: 35px;
}

button {
  width: 400px;
}

.card{
  /* height: 50px; */
  margin: 5px;
  max-height: 200px;
  max-width: 200px;
  width: 200px;
  height: 200px;
  /* width: 50px; */
  padding: 3px;
  text-align: center;
  border: 1px solid black;
  background-color: brown;
  color: white;
  overflow: scroll;
  /* white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; */
  /* background-color: crimson; */
}

::-webkit-scrollbar {
  display:none;
}